require('./bootstrap');

window.Vue = require('vue');

const app = new Vue({
    el: '#app',
    data: {
        errors: [],
        name : '',
        email: '',
        body: '',
    },
    methods: {
        addTask: function (e) {
            e.preventDefault();
            this.errors = [];
            axios.post('/tasks', {
                name : this.name,
                email : this.email,
                body: this.body
            })
            .then(function (response) {
                console.log(response);
            })
            .catch(function (response) {
                console.log(response);
            })
        }
    }
});